.dialog-enter {
  opacity: 0;
  -webkit-transform: translate3d(-50%, -50%, 0) scale(0.7);
  transform: translate3d(-50%, -50%, 0) scale(0.7);
}
.dialog-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(-50%, -50%, 0) scale(0.9);
  transform: translate3d(-50%, -50%, 0) scale(0.9);
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  line-height: 1.6;
  font-family: -apple-system-font, 'Helvetica Neue', sans-serif;
}

* {
  margin: 0;
  padding: 0;
}

a img {
  border: 0;
}

a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.weui-mask {
  position: fixed;
  z-index: 1000;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  -webkit-tap-highlight-color: transparent;
  background: rgba(0, 0, 0, 0.6);
}

.weui-mask_transparent {
  position: fixed;
  z-index: 1000;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.weui-dialog {
  position: fixed;
  z-index: 5000;
  width: 80%;
  max-width: 8rem;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background-color: #ffffff;
  text-align: center;
  border-radius: 0.08rem;
  overflow: hidden;
}

.weui-dialog__bd:first-child {
  padding: 1.08rem 0.533333rem 0.68rem;
  color: #353535;
}

.weui-dialog__bd {
  padding: 0 0.64rem 0.32rem;
  min-height: 1.066667rem;
  font-size: 0.4rem;
  line-height: 1.3;
  word-wrap: break-word;
  word-break: break-all;
  color: #808080;
}

.weui-dialog__title {
  font-weight: 400;
  font-size: 0.48rem;
}

.weui-dialog__hd {
  padding: 0.56rem 0.693333rem 0.213333rem;
}

.weui-dialog__ft {
  position: relative;
  line-height: 1.28rem;
  font-size: 0.48rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.weui-dialog__btn {
  display: block;
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
  color: #09bb07;
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  position: relative;
}

.weui-dialog__btn_primary {
  color: #09bb07;
}

.weui-dialog__ft:after {
  content: ' ';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 0.013333rem;
  border-top: 0.013333rem solid #d5d5d6;
  color: #d5d5d6;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
